import React, { useEffect } from "react";
import { useState } from "react";
import axios from "axios";
import AuthContext from "../../context/UserContext";
import { useContext } from "react";
import M from "materialize-css";

export default function Signin({history}) {
  // state of login informations
  const [account, setAccount] = useState(null);
  // state of app context
  const {userData, setuserData } = useContext(AuthContext);

  useEffect(() => {
    if (userData.user) {
      history.push("/home")
    }
  }, [userData, history])
  const handleChange = (e) => {
    setAccount((state) => {
      return {
        ...state,
        [e.target.name]: e.target.value,
      };
    });
  };
  const handleSubmit = (e) => {
    e.preventDefault();
    if (account) {
      axios
        .post(
          "https://alqubtan-planner.herokuapp.com/users/signin",
          account
        )
        .then((response) => {
          localStorage.setItem("user", JSON.stringify(response.data))
          setuserData(
            response.data,
          );
        })
        .catch((error) => {
          if (error.response) {
            M.toast({ html: "Invaild login information.", classes: "red" });
          } else {
            M.toast({ html: "Unable to login." });
          }
        });
    }
  };
  return (
    <div>
      <div className="container card-action blue lighten-2">
        <form
          className="signin-form container grey lighten-3"
          onSubmit={handleSubmit}
        >
          <h5 className="center">Login into your Account</h5>
          <div className="input-field">
            <label htmlFor="email">Email</label>
            <input type="email" name="email" onChange={handleChange} />
          </div>
          <div className="input-field">
            <label htmlFor="password">Password</label>
            <input type="password" name="password" onChange={handleChange} />
          </div>

          <input
            type="submit"
            value="Login"
            className="btn blue lighten-2"
          />
        </form>
      </div>
    </div>
  );
}

