import { BrowserRouter, Route } from "react-router-dom";
import Navbar from "./components/Navbar"
import Signup from "./pages/loggedOutPages/Signup";
import Signin from "./pages/loggedOutPages/Signin";

import Home from "./pages/loggedInPages/Home";
import Task from "./pages/loggedInPages/Task";
import AddTask from "./pages/loggedInPages/AddTask";
import Profile from "./pages/loggedInPages/Profile";

import UserContext from "./context/UserContext";
import { useState, useEffect,useContext } from "react";
import { useHistory } from "react-router-dom"

import AuthContext from "./context/UserContext";

const Routing = () => {
  // access to context
  const {userData, setuserData } = useContext(AuthContext)
  // to use push method
  const history = useHistory()
  useEffect(() => {
    const user = JSON.parse(localStorage.getItem("user"))

    if (user) {
      setuserData(user)
      history.push(history.location.pathname || "/home")
    } else {
      history.push("/login")
    }
  }, [setuserData, history])
  return (
    <div className="App">
        <Navbar />
        {/* logout pages */}
        <Route path= "/" exact component={userData.user ? Home : Signup}></Route>
        <Route path="/login" exact component={Signin}></Route>
        {/* login pages */}
        <Route path="/home" component={Home}></Route>
        <Route path="/tasks/:id" component={Task}></Route>
        <Route path="/create" component={AddTask}></Route>
        <Route path="/me" component={Profile}></Route>
      </div>
  )
}
function App() { 
  const [userData, setuserData] = useState({
    token: undefined,
    user: undefined
  })
  return (
    <BrowserRouter>
      <UserContext.Provider value={{userData, setuserData}}>
        <Routing/>
      </UserContext.Provider>
    </BrowserRouter>
  );
}

export default App;
