import React from "react";
import Task from "./Task";
import AuthContext from "../context/UserContext";
import { useContext, useState, useEffect } from "react";
import axios from "axios"
import M from "materialize-css"

export default function Tasks() {
  const { userData } = useContext(AuthContext);
  const [tasks, setTasks] = useState(undefined);
  useEffect(() => {
    if (userData) {
      axios
    .get(
      "https://alqubtan-planner.herokuapp.com/tasks/",
      {
        headers: {
          ContentType: "application/json",
          Authorization:
            "Bearer " + JSON.parse(localStorage.getItem("user")).token,
        },
      }
    )
    .then((response) => {
      setTasks(response.data);
    })
    .catch((err) => {
      M.toast({html: "Task not found", classes:"red"})
    });
    }
  })

  // Render th Ui

  return userData.user === undefined ? (
    ""
  ) : (
    <div className="container tasks-section">
      <div className="card">
        <div className="card-content">
          <div>Welcome, it's awesome to see you again {userData.user.name}</div>
          <ul className="collection with-header">
            {tasks ? (
              <div className="collection-header">
                <h5>You have some Todo's here</h5>
                {tasks.map(task => <Task task={task} key={task._id}/>)}
              </div>
            ): (
              <div className="collection-header">
                <h5>No Todo's left.</h5>
              </div>
            )}
            
          </ul>
        </div>
      </div>
    </div>
  );
}
