import axios from "axios";
import React, { useState } from "react";
import M from "materialize-css"
import {useHistory} from "react-router-dom"
export default function AddTask() {
  const history = useHistory()
  const [task, setTask] = useState({
    description: null,
    completed: false,
  });
  // Handle Text input changes
  const handleChange = (e) => {
    setTask((state) => {
      return {
        ...state,
        [e.target.name]: e.target.value,
      };
    });
  };
  // Handle True or false cheker
  const handleCheck = (e) => {
    if (e.target.checked) {
      setTask(state => {
        return {
          ...state,
          [e.target.name]: true
        }
      })
    } else {
      setTask(state => {
        return {
          ...state,
          [e.target.name]: false
        }
      })
    }
  }
  // Handle post Request
  const handleSubmit = (e) => {
    e.preventDefault();
    if (task) {
      axios.post("https://alqubtan-planner.herokuapp.com/tasks/create", task, {
        headers: {
          ContentType: "application/json",
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("user")).token
        }
      })
      .then(response => {
        M.toast({html: "Task created successfully", classes: "green"})
        history.push("/home")
      }).catch (err => {
        M.toast({html: "Task did not created", classes: "red"})
      })
    }
  };
  return (
    <div className="container addTask-section">
      <div className="card">
        <div className="card-content">
          <p className="center">Create new Task</p>
          {/* create task form */}
          <form onSubmit={handleSubmit}>
            <label htmlFor="description">Description</label>
            <input type="text" name="description" onChange={handleChange} />

            <div className="card-content">
              <p>
                <label>
                  <input type="checkbox" onChange={handleCheck} name="completed"/>
                  <span>Completed</span>
                </label>
              </p>
            </div>
            <input
              type="submit"
              value="Create"
              className="btn"
              onChange={handleSubmit}
            />
          </form>
        </div>
      </div>
    </div>
  );
}
