import React, { useEffect, useState, useContext } from "react";
import axios from "axios";
import AuthContext from "../../context/UserContext";
import M from "materialize-css";

export default function Task(history) {
  
  // user task from Server
  const [task, setTask] = useState("");
  const { userData } = useContext(AuthContext);
  // updated task functions
  const [updatedTask, setUpdatedTask] = useState({
    description: task.description,
    completed: false
  });
  // handle description change on (updated task)
  const handleChange = (e) => {
    setUpdatedTask((state) => {
      return {
        ...state,
        [e.target.name]: e.target.value,
      };
    });
  };
  // handle description update on updated task
  const handleCheck = (e) => {
    if (e.target.checked) {
      setUpdatedTask((state) => {
        return {
          ...state,
          [e.target.name]: true,
        };
      });
    } else {
      setUpdatedTask((state) => {
        return {
          ...state,
          [e.target.name]: false,
        };
      });
    }
  };

  useEffect(() => {
    let isMounted = true;
    const id = history.match.params.id;
    if (id) {
      axios
        .get(`https://alqubtan-planner.herokuapp.com/tasks/${id}`, {
          headers: {
            ContentType: "application/json",
            Authorization:
              "Bearer " + JSON.parse(localStorage.getItem("user")).token,
          },
        })
        .then((res) => {
          if (isMounted) setTask(res.data);
        })
        .catch(() => {
          history.history.push("/")
        });
      return () => {
        isMounted = false;
      };
    }
  }, [history]);
  // update task request
  const handleSubmitUpdate = (e) => {
    e.preventDefault();
    const id = history.match.params.id;
    if (id) {
      axios
        .patch(
          `https://alqubtan-planner.herokuapp.com/tasks/${id}`,
          updatedTask,
          {
            headers: {
              Authorization:
                "Bearer " + JSON.parse(localStorage.getItem("user")).token,
            },
          }
        )
        .then((res) => {
          M.toast({html: "Task updates successfuly", classes: "green"})
          history.history.push("/")
        })
        .catch((err) => {
          M.toast({ html: "Something went wrong.", classes: "red" });
        });
    }
  };
  const handleDelete = (e) => {
    e.preventDefault()
    const id = task._id;
    if (task) {
      axios.delete(`https://alqubtan-planner.herokuapp.com/tasks/${id}`, {
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("user")).token
        }
      }).then(res => {
        M.toast({html: "Task deleted successfuly", classes: "red"})
        history.history.push("/")
      }).catch((err) => {
        M.toast({ html: "Something went wrong.", classes: "red" });
      });
    }
  }
  return task ? (
    <div className="container task-form">
      <div className="card">
        <div className="card-content">
          <label htmlFor="description">Description: </label>
          <p name="description">"{task.description}"</p>
          <label htmlFor="completed">Completed: </label>
          <p name="completed">{task.completed.toString()}</p>
          <label htmlFor="createdAt">Created at</label>
          <p name="createdAt">{task.createdAt}</p>
          <label htmlFor="author">Author</label>
          <p name="author">{userData.user.name}</p>
        </div>
        {/*  Update Form */}
        <form className="card-content z-depth-2" onSubmit={handleSubmitUpdate}>
          <label htmlFor="description">Description :</label>
          <input
            type="text"
            placeholder="Update your task description"
            name="description"
            onChange={handleChange}
          />
          <p>
            <label>
              <input type="checkbox" onChange={handleCheck} name="completed" />
              <span>Completed</span>
            </label>
          </p>
          <br />
          <input
            type="submit"
            value="Update"
            className="waves-effect waves-light btn btn-margin"
          />

          <input
            type="submit"
            value="Delete"
            className="waves-effect waves-light btn red"
            onClick={handleDelete}
          />
        </form>
      </div>
    </div>
  ) : (
    <div className="card-content center">
      <p>Loading...</p>
    </div>
  );
}
