import React from "react";
import { useState } from "react";
import axios from "axios";
import { useHistory } from "react-router-dom";
import M from "materialize-css";
export default function Signup() {
  // state of signup information
  const [newUser, setNewUser] = useState(null);

  const handleChange = (e) => {
    setNewUser((state) => {
      return {
        ...state,
        [e.target.name]: e.target.value,
      };
    });
  };
  // get history from react router dom
  const history = useHistory();
  const handleSubmit = (e) => {
    e.preventDefault();
    if (newUser) {
      axios
        .post(
          "https://alqubtan-planner.herokuapp.com/users/signup",
          newUser
        )
        .then(function (response) {
          M.toast({
            html: "Account has been created successfully.",
            classes: "green",
          });
          history.push("/login");
        })
        .catch(function (error) {
          console.log(error)
          if (error) {
            if (error.response.data.errors) {
              return Object.keys(error.response.data.errors).forEach((err) => {
                M.toast({
                  html: error.response.data.errors[err].message,
                  classes: "red",
                });
              });
            } 

            M.toast({html: "Account already registered.", classes: "red"})
            
            
          } else {
            M.toast({ html: "Unable to create an account.", classes: "red" });
          }
        });
    }
  };
  return (
    <div>
      <div className="container card-action blue lighten-2">
        <form
          onSubmit={handleSubmit}
          className="signup-form container grey lighten-3"
        >
          <h5 className="center">Let's Create an Account</h5>
          <p className="center">Get more done with the our Tasks app.</p>
          <div className="input-field">
            <label htmlFor="name">Full name</label>
            <input type="text" name="name" onChange={handleChange} />
          </div>
          <div className="input-field">
            <label htmlFor="email">Email</label>
            <input type="email" name="email" onChange={handleChange} />
          </div>
          <div className="input-field">
            <label htmlFor="password">Password</label>
            <input type="password" name="password" onChange={handleChange} />
          </div>
          <div className="input-field">
            <label htmlFor="age">Age (optionally)</label>
            <input type="number" name="age" onChange={handleChange} />
          </div>
          <input
            type="submit"
            value="Create account"
            className="blue lighten-2 btn"
          />
        </form>
      </div>
    </div>
  );
}
