import React, { useEffect, useState, useContext } from "react";
import axios from "axios";
import M from "materialize-css";
import { useHistory } from "react-router-dom";
import AuthContext from "../../context/UserContext";

export default function Profile() {
  const { setuserData } = useContext(AuthContext);
  const history = useHistory();
  const [profile, setuserProfile] = useState("");
  const [userUpdates, setuserUpdates] = useState("");

  useEffect(() => {
    axios
      .get("https://alqubtan-planner.herokuapp.com/me", {
        headers: {
          Authorization:
            "Bearer " + JSON.parse(localStorage.getItem("user")).token,
        },
      })
      .then((res) => {
        setuserProfile(res.data);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);
  const handleChange = (e) => {
    setuserUpdates((state) => {
      return {
        ...state,
        [e.target.id]: e.target.files ? e.target.files[0] : e.target.value
      };
    });
  };
  const handleSubmit = (e) => {
    e.preventDefault();
    if (userUpdates) {
      axios
        .patch("https://alqubtan-planner.herokuapp.com/me", userUpdates, {
          headers: {
            Authorization:
              "Bearer " + JSON.parse(localStorage.getItem("user")).token,
          },
        })
        .then((res) => {
          M.toast({ html: "Updated successfuly.", classes: "green" });
          history.push("/");
        })
        .catch((err) => {
          M.toast({ html: "Something went worng", classes: "red" });
        });
    }
  };
  const handleDelete = (e) => {
    e.preventDefault();
    axios
      .delete("https://alqubtan-planner.herokuapp.com/me", {
        headers: {
          Authorization: JSON.parse(localStorage.getItem("user")).token,
        },
      })
      .then((res) => {
        localStorage.clear();
        setuserData("");
        history.push("/");
      })
      .catch((err) => {
        M.toast({ html: "Something went wrong", classes: "red" });
      });
  };

  return profile ? (
    <div className="container profile-section">
      <div className="card">
        <div className="card-content">
          <p className="center">Account Informations</p>
          <form onSubmit={handleSubmit}>
            <label htmlFor="name">Update name</label>
            <input
              type="text"
              name="name"
              id="name"
              defaultValue={profile.name}
              onChange={handleChange}
            />
            <label htmlFor="email">Update Email</label>
            <input
              type="email"
              name="email"
              id="email"
              defaultValue={profile.email}
              onChange={handleChange}
            />
            <label htmlFor="age">Update Age</label>
            <input
              type="number"
              name="age"
              id="age"
              defaultValue={profile.age}
              onChange={handleChange}
            />
            <label htmlFor="password">Update Password</label>
            <input
              type="password"
              name="password"
              id="password"
              onChange={handleChange}
            />
            <input
              type="submit"
              value="Save chanages"
              className="waves-effect waves-light btn"
            />
          </form>
          <p className="center">Delete user profile</p>
          <form action="" onSubmit={handleDelete}>
            <input
              type="submit"
              value="delete profile"
              className="waves-effect waves-light red btn"
            />
          </form>
          
        </div>
      </div>
    </div>
  ) : (
    <div className="card-content center">
      <p>Loading...</p>
    </div>
  );
}
